import React from 'react';
import { CubeIcon } from "@heroicons/react/solid";
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'
import { product } from '../data';

const spanStyle = {
  padding: '20px',
  background: '#efefef',
  color: '#000000'
}

const divStyle = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  backgroundSize: 'cover',
  height: '50vh',
  borderRadius:"20px"
}

const center ={
textAlign:'center',
marginTop:'15px'
}

export default function Product(){

    return (
        <div id="works" className="slide-container slideshow 	">
          <div className="container px-5 py-10 mx-auto">
        <div className="text-center mb-20">
          <CubeIcon className="w-10 inline-block mb-4" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font text-white mb-2">
            Product Design 
          </h1>
        </div>
        <Slide>
         {product.map((product)=> (
           <div key={product.title}>
            <a href ="">
              <div style={{ ...divStyle, 'backgroundImage': `url(${product.src})` }}>
                {/* <span style={spanStyle}>{projectArch.title}</span> */}
              </div>
                <p style={center}>{product.description}</p>
            </a>
            </div>
          ))} 
        </Slide>
      </div>
      </div>
    )
}




